import React from 'react';
import { Layout, Wrapper, CardHeader } from '../components';
import Helmet from 'react-helmet';
import { CardContent } from '@material-ui/core';
import styled from 'styled-components';
import { graphql } from 'gatsby';

const HTMLTitle = '更新记录';

interface Props {
  data: {
    allChangeLogYaml: any;
  };
}

interface States {
  allChangeLog: [];
  title: string;
}

const Article = styled.article`
  h3 {
    margin: 64px 0;
  }
`;

const Chunk = styled.section`
  margin-bottom: 48px;
  h4 {
    margin-bottom: 24px;
  }
  span {
    color: #919191;
    font-size: 12px;
    margin-top: -16px;
    margin-bottom: 16px;
    display: block;
  }
  p {
    font-size: 14px;
    margin-bottom: 8px;
  }
`;

export default class ChangeLogPage extends React.Component<Props, States> {
  constructor(props: any) {
    super(props);
    this.state = {
      allChangeLog: this.props.data.allChangeLogYaml.edges,
      title: HTMLTitle,
    };
  }

  getLocalTime(ns: any) {
    return new Date(ns).toLocaleDateString().replace(/:\d{1,2}$/, '');
  }

  public render() {
    const { title, allChangeLog } = this.state;
    return (
      <Layout>
        <Helmet title={title} />
        <Wrapper>
          <CardHeader>
            <h2>{title}</h2>
          </CardHeader>
          <CardContent>
            <Article>
              {!!allChangeLog &&
                allChangeLog.map((log: any) => {
                  return (
                    <Chunk key={log.node.version}>
                      <h4>{log.node.version}</h4>
                      <span>{this.getLocalTime(log.node.date)}</span>
                      {log.node.content.map((li: string) => {
                        return <p key={li}>{li}</p>;
                      })}
                    </Chunk>
                  );
                })}
            </Article>
          </CardContent>
        </Wrapper>
      </Layout>
    );
  }
}

export const AllChangeLogQuery = graphql`
  query {
    allChangeLogYaml(sort: { order: [DESC], fields: [date] }) {
      edges {
        node {
          version
          date
          content
        }
      }
    }
  }
`;
